/* desktop and mobile common layout */
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  color: white;
}

body{
  overflow: visible;
  background-color: rgb(32, 32, 32);
  z-index: -2;
}

.bunch-of-logos{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  /* border: 5px solid orange; */
  text-align: center;
}

.bunch-of-logos > .one-logo:hover{
  border:1px solid red
}

.bunch-of-logos > .one-logo > img{
  width: 100%;
  object-fit: cover;
  clip-path: circle(50%);
}

.inactive{
  opacity: 60%;
  filter: grayscale(100%) invert(100%);
  /* transform: scale(0.6); */
}

.tooltip-yAxis-1{
  font-size: calc(1em);
}

.stream-xAxis{
  opacity: 0.5;
}

.stream-yAxis{
  opacity: 0.5;
}
.stream-yAxis > .domain{
  stroke-width: 0;
}

.tooltip-yAxis-1>.domain{
  stroke-width: 0;
}
.tooltip-yAxis-1>.tick>line{
  opacity: 0.5;
}
.tooltip-yAxis-1>.tick>text{
  font-size: 0.6em;
  /* transform: translate(0,-0.6em); */
}
.tooltip-yAxis-1>.tick:first-child>text{
  font-size: 0em;
  /* transform: translate(0,-0.6em); */
}

