.header.browser{
  width:100%;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  background-color:black;
  display: flex;
  flex-direction: column;
}

.header.browser>h3{
  font-size: calc(20px);
}
.header.browser>p{
  font-size: calc(15px);
}

.separator{
  opacity: 0.5;
  width: 90%;
}

.window-container.browser{
  display: flex;
  flex-direction: column;
  width: 80%;
  /* border:2px solid red */
}

/* @media only screen and (max-width: 800px) {
