.bunch-of-logos.browser{
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  /* align-items: center; */
  flex-wrap: wrap;
  width: 100%;
  /* border: 5px solid orange; */
  /* text-align: center; */
}

.streamgraph.browserView{
  display: flex;
  overflow: visible;
  z-index: 2;
  padding:1em 1em 1em 1em;
  margin-top: 5%;
