} */

.streamgraph-wrapper.browser{
  width: 100%;
  display: flex;
  overflow: visible;
}

.streamgraph-svg.browser{
  width: 100%;
  display: flex;
  overflow: visible;
}

.side-graph.browserView{
  /* border: 2px solid red; */
