}

.tooltip.browserView{
  background-color: grey;
  width: 28%;
  min-width: 360px;
  height:38%;
  position: absolute;
  margin-left: 0%;
  display: flex;
  flex-direction: column;
  
}  

.tooltip-wrapper.browser{
  /* border:2px solid red; */
  height: 50%;
  display: flex;
  width: 60%;
  flex-direction: column;
}  

.tooltip-svg.browser{
  /* border: 2px solid cyan; */
  /* height:100%; */
  /* display: flex; */
  /* width: 100%; */
}  

.tooltip-title.browser{
  font-size: calc(10px + .1vw);
  font-weight: 600;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}  

