.app.mobileView{
  display: flex;
  flex-direction: column;
  /* border: 4px solid black; */
}

.header.mobile{
  margin-left:5%;
  width: 90%;
}

.header.mobile > h4{
  width:100%;
  font-size: calc(18px + 5vw);
  color: lightblue;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.header.mobile > p{
  font-size: calc(25px);
  color: lightcoral;
  font-weight: 300;
}

.controls-container.mobile{
  display: flex;
  flex-direction: column;
  width: 100%;
  /* border: 2px solid red; */
}

.university-logos.mobile, .college-logos.mobile{
  padding: 1em 1em 0em 1em;
}

.logos-header.mobile{
  /* border-top: 1px solid white; */
  margin-bottom: 2%;
}

.logos-title.mobile{
  font-weight: 600;
  margin-bottom: 0.2em;
  text-transform: uppercase;
}

.logos-all.mobile,.toggle-show.mobile{
  background-color: black;
  font-size:15px;
  padding:0.1em;
  margin-bottom: 0.7em;
  text-transform: uppercase;
}

.one-logo.mobile{
  display: flex;
  flex-basis: 10%;
  /* border: 1px solid red; */
  margin: 2px;
  transition: 500ms;
  object-fit: cover;
}

.display{
